import React, {useState, useEffect } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import {AppBar, Toolbar, Button, Link} from '@material-ui/core';
import CustomTable from '../../components/Table/Table'
import userService from '../../services/users'

const styles = {
  AppBarClass: {
    marginTop: "-80px",
    backgroundColor: "#00acc1"
  }
};

const useStyles = makeStyles(styles);

export default function UsersTable() {
  const [users, setUsers] = useState([['','', '']])

  useEffect(() => {
    const fetchUsers = async () => {
      const result = await userService.getAll()
      result.forEach((item) => {
        delete item.id
        delete item.password
        item.activo = item.activo.toString()
        item.rol = item.id_rol.nombre_rol
        item.primer_nombre = item.id_info.primer_nombre
        delete item.id_info
        delete item.id_rol
        
        
      })

      setUsers(result.map(Object.values))
    }

    fetchUsers()
  }, [])

  console.log(users)

  const classes = useStyles();
  return (
    <React.Fragment>
      <CustomTable tableData={users} tableHead={['Activo', 'Email']}/>
    </React.Fragment>
  );
}